@media screen and (min-width: 769px) {
  .sectionSP {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .sectionPC {
    display: none;
  }
}
.history {
  position: relative;
  overflow: hidden;
}
.titlePC {
  position: absolute;
  top: 9vw;
  left: 0;
  transition: transform 0.3s ease;
}
.yeaer2015,
.yeaer2016,
.yeaer2017,
.yeaer2018,
.yeaer2019,
.yeaer2020,
.yeaer2021,
.yeaer2022,
.yeaer2023 {
  flex-shrink: 0;
}
.yeaer2015 {
  min-width: 38vw;
}
.yeaer2016 {
  min-width: 30vw;
}
.yeaer2017 {
  min-width: 30vw;
}
.yeaer2018 {
  min-width: 35vw;
}
.yeaer2019 {
  min-width: 50vw;
}
.yeaer2020 {
  min-width: 48vw;
}
.yeaer2021 {
  min-width: 30vw;
}
.yeaer2022 {
  min-width: 48vw;
}
.yeaer2023 {
  min-width: 55vw;
}
.historyArea {
  display: flex;
  align-items: flex-start;
  min-height: 230px;
}
.historyArea.up {
  border-bottom: 5px solid var(--pointColorBlue);
}
.historybox {
  display: flex;
  align-items: flex-start;
  padding-bottom: 4rem;
  padding-left: 2rem;
  box-sizing: border-box;
  position: relative;
  height: 100%;
}
.historybox .maruten {
  border: 5px solid var(--pointColorBlue);
  background-color: #fff;
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  left: -13px;
  bottom: -16px;
  z-index: 1;
}
.historyArea.down .historybox .maruten {
  bottom: auto;
  top: -16px;
}
.historyArea.down .historybox {
  align-items: flex-end;
  padding-bottom: 0;
  padding-top: 4rem;
}
.historybox::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  border-left: 5px dashed #b9c7dd;
  height: 100%;
}
.historybox::before {
  content: '';
  position: absolute;
  left: -5px;
  top: -5px;
  width: 17px;
  height: 17px;
  background-color: #b9c7dd;
  border-radius: 50%;
}
.historyArea.up .historybox::after {
  bottom: 0;
  top: auto;
}
.historyArea.down .historybox::before {
  top: auto;
  bottom: -5px;
}
.yeaer2015 .historyArea.down {
  margin-left: 12vw;
}
.yeaer2019 .historyArea.up {
  padding-left: 21vw;
}
.historybox img {
  max-height: 20vh;
  margin-left: 1rem;
  margin-right: 6rem;
}
.yearImg img {
  max-width: 300px;
  width: 16vw;
}
.textArea .month {
  font-size: var(--fz36);
  font-weight: 600;
  color: var(--pointColorBlue);
}
.textArea .month span {
  font-size: var(--fz16);
}
.textArea .text {
  color: #275aa9;
  line-height: 1.2;
}
@media screen and (max-width: 820px) {
  .yearImg img {
    max-width: 300px;
    width: 25vw;
  }
}
@media screen and (max-width: 768px) {
  .containerSP {
    position: relative;
  }
  .historyArea {
    margin: 4rem 0;
    padding: 2rem 0;
    border-left: 5px solid var(--pointColorBlue);
  }
}
@media screen and (max-width: 640px) {
  .yearImg img {
    max-width: 300px;
    width: 40vw;
  }
}
